export interface FormsProps {}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      width: '100%',
      overflowX: 'auto'
    },
    table: {
      boxShadow:
        '0px 2px 1px -1px rgba(0,0,0,0.2),0px 1px 1px 0px rgba(0,0,0,0.14),0px 1px 3px 0px rgba(0,0,0,0.12)',
      minWidth: 600
    }
  })
)

function createData(
  id: string,
  firstName: string,
  middleName: string,
  lastName: string,
  phone: string,
  email: string
) {
  return { id, firstName, middleName, lastName, phone, email }
}

const Forms: React.SFC<FormsProps> = () => {
  const classes = useStyles(useTheme())

  const rows = db.map(rec =>
    createData(
      rec.id,
      rec.partner.firstName,
      rec.partner.middleName,
      rec.partner.lastName,
      rec.partner.phone,
      rec.partner.email
    )
  )

  const handleClick = (event: any, index: number) => {
    console.log(index)
  }

  return (
    <Layout title='PDF Forms Filler'>
      <CssBaseline />
      <Container>
        <div className={classes.paper}>
          <Toolbar>
            <Typography variant='h5'>Mock Database</Typography>
          </Toolbar>
          <Table className={classes.table} aria-label='simple table'>
            <TableHead>
              <TableRow>
                <TableCell>ID</TableCell>
                <TableCell align='right'>Fist Name</TableCell>
                <TableCell align='right'>Last Name</TableCell>
                <TableCell align='right'>Phone</TableCell>
                <TableCell align='right'>Email</TableCell>
                <TableCell align='right'>View</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row, index) => (
                <TableRow hover key={row.id}>
                  <TableCell component='th' scope='row'>
                    {row.firstName}
                  </TableCell>
                  <TableCell align='right'>{row.middleName}</TableCell>
                  <TableCell align='right'>{row.lastName}</TableCell>
                  <TableCell align='right'>{row.phone}</TableCell>
                  <TableCell align='right'>{row.email}</TableCell>
                  <TableCell align='right'>
                    <IconButton
                      edge='start'
                      onClick={() => handleClick(event, index)}
                      color='inherit'
                      aria-label='home'>
                      <HomeIcon color='action' />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </Container>
    </Layout>
  )
}

export default Forms
